import React, { Component } from 'react';

export default class Masterbrand extends Component {
  
  render(){
    const data = this.props.data;

    return(
      <section className="mainSection masterbrand">
        <h2 className="heading fadeUp">Here at Silver Fern Farms, were real food people, were your red meat partner, and we believe that delicious starts with you.</h2>
        <p className="paragraph fadeUp">It starts with taking the very best raw ingredients and using them as a source of inspiration. With adding your skill and craft as real food professionals to create something truly special. With sharing stories of our source with your customers, connecting them to where it came from and what made it delicious. With creating extraordinary eating experiences. With care, respect and passion. This is what we love about foodservice.</p>
        <img src="./images/masterbrand.jpg" alt="" className="mainSection-img masterbrand-img"/>
        <video src="./videos/masterbrand.mp4" className="mainSection-video masterbrand-video" autoPlay muted loop/>
      </section>
    )
  }
}


