import React, { Component } from 'react';
import { TweenLite } from "gsap/TweenMax";

export default class Sustainability extends Component {
  currActive = 0;
  currMove = 0;
  threshold = 0.2;
  momentum = 0.5;

  componentDidMount(){
    this.slider = document.querySelector(".sustainability-slider");
    this.slides = document.querySelectorAll(".sustainability-slide");
    this.navItems = document.querySelectorAll(".sustainability-nav-item");
    this.allVideos = document.querySelectorAll("video");
    this.slidePositions = [];
    this.windowW = window.innerWidth;

    this.slides.forEach((slide, i) => {
      slide.style.left = 100 * i + "vw";
    })

    this.navItems.forEach((item, i) => {
      this.slidePositions.push(i * this.windowW * -1);
    })
  }

  navClicked(i){
    this.navItems[this.currActive].classList.remove("-active");

    TweenLite.to(this.slider, 0.7, {x: this.slidePositions[i]});

    this.currMove = this.slidePositions[i];
    this.currActive = i;
    this.navItems[this.currActive].classList.add("-active"); 
  }

  down(e){
    this.pressed = true;
    this.startX = e.touches[0].clientX;
    this.startMove = this.startX - this.currMove;

    this.allVideos.forEach(video => {
      video.pause();
    })
  }

  up(){
    this.pressed = false;

    if(
      this.currActive < this.slidePositions.length - 1 && 
      this.currMove < this.slidePositions[this.currActive] - ((this.windowW / 2) * this.threshold)
    ){
      this.navItems[this.currActive].classList.remove("-active");
      this.currActive++;
    } else if(
      this.currActive > 0 && 
      this.currMove > this.slidePositions[this.currActive] + ((this.windowW / 2) * this.threshold)
    ){
      this.navItems[this.currActive].classList.remove("-active");
      this.currActive--;
    }

    this.currMove = this.slidePositions[this.currActive];
    this.navItems[this.currActive].classList.add("-active");
    TweenLite.to(this.slider, 0.7, {x: this.currMove, onComplete: () => {
      setTimeout(() => {
        this.allVideos.forEach(video => {
          video.play();
        })
      }, 100)
    }});
  }

  move(e){
    if(!this.pressed) return;

    let x = e.touches[0].clientX,
        readjust = (this.startX - x) * this.momentum;

    x -= this.startMove;

    this.currMove = x - readjust;

    TweenLite.to(this.slider, 0, {x: this.currMove});
  }
  
  render(){
    const data = this.props.data;

    return(
      <section className="mainSection sustainability">
        <div className="sustainability-slider"
          onTouchStart={e => this.down(e)}
          onTouchEnd={() => this.up()}
          onTouchMove={e => this.move(e)}
        >
          <div className="sustainability-slide">
            <h2 className="heading fadeUp">Our commitment to sustainability.</h2>
            {/* <p className="sustainability-divider"></p> */}
            <p className="paragraph sustainability-paragraphOne fadeUp">We believe Silver Fern Farms can create positive change for our environment, our people, our communities and the economic success of New Zealand. We call this <b>Our Sustainable Chain of Care.</b></p>
            <img src="./images/sustainability-1.jpg" alt="" className="mainSection-img sustainability-imgOne"/>
            <img src="./images/sustainability-mark.svg" alt="" className="sustainability-mark"/>
            <video src="./videos/sustainability-1.mp4" className="mainSection-video sustainability-videoOne" autoPlay muted loop/>
          </div>

          <div className="sustainability-slide">
            <p className="paragraph sustainability-paragraphTwo fadeUp">As part of our <b>operational efficiency goals</b> we are working with our product packaging suppliers, so that in time we can transition to alternative sustainable packaging materials that can deliver meat shelflife requirements our partners require.</p>
            <p className="paragraph sustainability-paragraphThree fadeUp">As part of our <b>food safety and quality goals</b>, we have partnered with Oritain, an independent company that scientifically proves the origin of our products. By measuring naturally occurring elements inside the food, this protects customers, protects our brand, and verifies that every product is true to its New Zealand origin claim.</p>
            <img src="./images/sustainability-2.png" alt="" className="mainSection-img sustainability-imgTwo"/>
            <img src="./images/sustainability-oritain.svg" alt="" className="sustainability-oritain"/>
            <video src="./videos/sustainability-2.mp4" className="mainSection-video sustainability-videoTwo" autoPlay muted loop/>
          </div>

          <div className="sustainability-slide">
            <p className="paragraph sustainability-paragraphFour fadeUp">We now track the downward trend of plastic use and in 2017 we removed 23.8 tonne of plastic without sacrificing food safety, shelf-life or creating additional food waste.</p>
            <video src="./videos/sustainability-big.mp4" className="mainSection-video sustainability-videoThree" autoPlay muted loop/>
          </div>
        </div>

        <div className="sustainability-nav">
          <div className="sustainability-nav-item -active" style={{borderColor: "#000"}} onClick={() => this.navClicked(0)}>Our Commitment</div>
          <div className="sustainability-nav-item" style={{borderColor: "#000"}} onClick={() => this.navClicked(1)}>Oritain</div>
          <div className="sustainability-nav-item" style={{borderColor: "#000"}} onClick={() => this.navClicked(2)}>Plastic Use</div>
        </div>
      </section>
    )
  }
}


