import React, { Component } from 'react';

export default class Source extends Component {
  
  render(){
    const data = this.props.data;

    return(
      <section className="mainSection source">
        <h2 className="heading fadeUp">We understand the power of storytelling and the inspiration a real connection to source can provide  to you and your customers.</h2>
        <p className="paragraph fadeUp">Thats why we bring you more than a selection of the very best grass-fed red meat, from the very best source. We connect you to where it came from; the places, the people, the passion. Sharing real stories from the land and people behind the food so that you can do the same.</p>
        <img src="./images/source.jpg" alt="" className="mainSection-img source-img"/>
        <video src="./videos/source.mp4" className="mainSection-video source-video" autoPlay muted loop/>
      </section>
    )
  }
}


