import React, { Component } from 'react';
import Meat from './Meat';
import { TweenLite } from "gsap/TweenMax";

export default class Meats extends Component {
  startMove = 0;
  currMove = 0;
  currActive = 0;
  pressed = false;
  momentum = 0.5;
  threshold = 0.2;

  componentDidUpdate(){
    this.meatContain = document.querySelector(".meats");
    this.meats = document.querySelectorAll('.meat');
    this.mainNavList = document.querySelectorAll('.nav-subList-item');
    this.windowW = window.innerWidth;

    this.currActive = this.props.currActive;
    this.currMove = this.currActive * this.windowW * -1;

    if(this.currActive === false) return;

    this.meats.forEach((meat, i) => {
      meat.style.transform = `translate(calc(${i * 100}vw), -50%)`;
    })

    this.meatContain.style.width = 100 * this.meats.length + "vw";
    TweenLite.to(this.meatContain, 0, {x: this.currMove});

    this.slidePositions = [];
    this.meats.forEach((el, i) => {
      this.slidePositions.push(this.windowW * i * -1);
    })

    this.navItems = document.querySelectorAll(".meats-nav-item");
    this.navItems.forEach(item => item.classList.remove("-active"));
    this.navItems[this.currActive].classList.add("-active");
    this.meats[this.currActive].classList.add("-focused");
    this.mainNavList.forEach(item => item.classList.remove("-active"));
    this.mainNavList[this.currActive].classList.add("-active");
  }

  down(e){
    if(e.target.classList.value.length < 1 || e.target.classList[0].indexOf("squeegie") !== -1) return;
    this.pressed = true;
    this.startX = e.touches[0].clientX;
    this.startMove = this.startX - this.currMove;
  }

  up(){
    this.pressed = false;

    if(
      this.currActive < this.slidePositions.length - 1 && 
      this.currMove < this.slidePositions[this.currActive] - ((this.windowW / 2) * this.threshold)
    ){
      this.mainNavList[this.currActive].classList.remove("-active");
      this.meats[this.currActive].classList.remove("-focused");
      this.navItems[this.currActive].classList.remove("-active");
      this.currActive++;
    } else if(
      this.currActive > 0 && 
      this.currMove > this.slidePositions[this.currActive] + ((this.windowW / 2) * this.threshold)
    ){
      this.mainNavList[this.currActive].classList.remove("-active");
      this.meats[this.currActive].classList.remove("-focused");
      this.navItems[this.currActive].classList.remove("-active");
      this.currActive--;
    }

    this.meats[this.currActive].classList.add("-focused");
    this.currMove = this.slidePositions[this.currActive];
    this.navItems[this.currActive].classList.add("-active");
    this.mainNavList[this.currActive].classList.add("-active");
    TweenLite.to(this.meatContain, 0.4, {x: this.currMove});
  }

  move(e){
    if(!this.pressed) return;

    let x = e.touches[0].clientX,
        readjust = (this.startX - x) * this.momentum;

    x -= this.startMove;

    this.currMove = x - readjust;

    TweenLite.to(this.meatContain, 0.2, {x: this.currMove});
  }

  navClicked(i){
    this.meats[this.currActive].classList.remove("-focused");
    this.navItems[this.currActive].classList.remove("-active");
    this.mainNavList[this.currActive].classList.remove("-active");
    this.currActive = i;
    this.currMove = this.slidePositions[this.currActive];
    this.meats[this.currActive].classList.add("-focused");
    this.navItems[this.currActive].classList.add("-active");
    this.mainNavList[this.currActive].classList.add("-active");
    TweenLite.to(this.meatContain, 0.4, {x: this.currMove});
  }
  
  render(){
    const data = this.props.data;

    this.navItems = [
      {name: "RESERVE", color: "#444344"},
      {name: "ANGUS", color: "#d34727"},
      {name: "CERVENA", color: "#a89c62"},
      {name: "NZ BEEF", color: "#026a38"},
      {name: "NZ VENISON", color: "#772840"},
      {name: "NZ LAMB", color: "#47a148"}
    ]

    return(
      <React.Fragment>
        <div className="meats" 
          onTouchStart={e => this.down(e)}
          onTouchEnd={() => this.up()}
          onTouchMove={e => this.move(e)}
        >
          <Meat data={{
            type: 'reserve', 
            titleImg: './images/titles/Title-Reserve.svg', 
            mountains:['./images/squeegie/Beef-Reserve/bottom.svg','./images/squeegie/Beef-Reserve/top.svg'], 
            old:'./images/squeegie/Beef-Reserve/old.png', 
            new:'./images/squeegie/Beef-Reserve/new.png',
            title: 'The Premier Selection',
            bottomImgs: ['./images/reserve/1.jpg','./images/reserve/2.jpg','./images/reserve/3.jpg'],
            paragraphs: ['Extraordinary eating quality, guaranteed.','Not all beef is created equal. Silver Fern Farms Reserve is an elite standard of grass-fed beef. Our promise of consistent tenderness, juiciness and flavour is backed by our Eating Quality System. Our Master Graders hand-select the best based on proven scientific criteria.','A truly exclusive range, this is the top 4% of New Zealand grass-fed beef.'],
            icons: ['./images/icons/Icon-No Hormones_Black.svg','./images/icons/Icon_Aged 21 days_Black.svg','./images/icons/Icon_PastureRaised_GrassFed_black.svg'],
            info: ['Elite standard in beef | Guaranteed tender','Hand-selected for consistent Eating Quality','Only the top 4% of New Zealand beef makes Reserve grade']
          }}/>

          <Meat data={{
            type: 'angus', 
            titleImg: './images/titles/Title-Angus.svg', 
            mountains:['./images/squeegie/Angus/bottom.svg','./images/squeegie/Angus/top.svg'], 
            old:'./images/squeegie/Angus/old.png', 
            new:'./images/squeegie/Angus/new.png',
            title: 'THE BOLDEST BEEF FLAVOUR',
            bottomImgs: ['./images/angus/1.jpg','./images/angus/2.jpg','./images/angus/3.jpg'],
            paragraphs: ['Full-bodied and powerful; the true taste of Angus beef.','Silver Fern Farms Angus brings the perfect combination of flavour, texture and juiciness with every cut. Natural, with grass-fed marbling, the distinctive boldness of Angus beef at its best.', 'The Angus breed brought to its bold best in New Zealand by the skill of our farmers and the quality of our wide-open pastures.'],
            icons: ['./images/icons/Icon_PastureRaised_GrassFed_black.svg','./images/icons/Icon-No Hormones_Black.svg','./images/icons/Icon-Angus_USETHIS.svg'],
            info: ['Full-bodied distinctive flavour | Grass-fed to greatness','Natural marbling | Aged to perfection']
          }}/>

          <Meat data={{
            type: 'cervena', 
            titleImg: './images/titles/Title-Cervena.svg', 
            mountains:['./images/squeegie/Cervena/bottom.svg','./images/squeegie/Cervena/top.svg'], 
            old:'./images/squeegie/Cervena/old.png', 
            new:'./images/squeegie/Cervena/new.png',
            title: 'PASTURE-RAISED FRESHNESS',
            bottomImgs: ['./images/cervena/1.jpg','./images/cervena/2.jpg','./images/cervena/3.jpg'],
            paragraphs: ['Free-farmed venison with a fresh refined flavour and tenderness.','Silver Fern Farms Cervena is raised naturally and sustainably on open pastures. Carefully selected, our range of high-quality cuts will inspire new creations and fresh takes on classic cuisine. Quality assured standards so strict, there is consistency in every cut.'],
            icons: ['./images/icons/Icon-No Hormones_Black.svg','./images/icons/Icon-Cervena_Diamond_black.svg'],
            info: ['Free-farmed | Pasture-raised, grass-fed | Selected from younger deer','Refined flavour and freshness | Consistently tender','Selection of premium cuts']
          }}/>

          <Meat data={{
            type: 'nzBeef', 
            titleImg: './images/titles/Title-NZBeef.svg', 
            mountains:['./images/squeegie/NZ-Beef/bottom.svg','./images/squeegie/NZ-Beef/top.svg'], 
            old:'./images/squeegie/NZ-Beef/old.png', 
            new:'./images/squeegie/NZ-Beef/new.png',
            title: 'FULL-FLAVOURED FINE TEXTURED',
            bottomImgs: ['./images/nzBeef/1.jpg','./images/nzBeef/2.jpg','./images/nzBeef/3.jpg'],
            paragraphs: ['Silver Fern Farms Beef is grass-fed, pasture-raised, creating the full-flavoured, naturally lean and nutritious qualities that beef lovers look for. Our range of highquality cuts deliver the flavour and texture of beef as it should be, every-time.'],
            icons: ['./images/icons/Icon_PastureRaised_GrassFed_black.svg','./images/icons/Icon-No Hormones_Black.svg','./images/icons/Icon-Halal_Logo_Black_2018.svg'],
            info: ['Grass-fed, pasture-raised | Full-flavoured, fine textured','Naturally nutritious | Halal certified']
          }}/>

          <Meat data={{
            type: 'nzVenison', 
            titleImg: './images/titles/Title-NZVenison.svg', 
            mountains:['./images/squeegie/NZ-Venison/bottom.svg','./images/squeegie/NZ-Venison/top.svg'], 
            old:'./images/squeegie/NZ-Venison/old.png', 
            new:'./images/squeegie/NZ-Venison/new.png',
            title: 'LIGHT, FRESH AND DELICATE',
            bottomImgs: ['./images/nzVenison/1.jpg','./images/nzVenison/2.jpg','./images/nzVenison/3.jpg'],
            paragraphs: ['Silver Fern Farms Venison is grass-fed and pasture-raised in New Zealand to create fresh and delicate flavour.','Supremely lean, healthy and sustainably raised. A versatile flavour that opens new possibilities for familiar cuts.'],
            icons: ['./images/icons/Icon_PastureRaised_GrassFed_black.svg'],
            info: ['Grass-fed, pasture-raised | Fresh, delicate flavour','Supremely lean and healthy | Versatile selection of cuts']
          }}/>

          <Meat data={{
            type: 'nzLamb', 
            titleImg: './images/titles/Title-NZLamb.svg', 
            mountains:['./images/squeegie/NZ-Lamb/bottom.svg','./images/squeegie/NZ-Lamb/top.svg'], 
            old:'./images/squeegie/NZ-Lamb/old.png', 
            new:'./images/squeegie/NZ-Lamb/new.png',
            title: 'SUBTLY SWEET AND TENDER',
            bottomImgs: ['./images/nzLamb/1.jpg','./images/nzLamb/2.jpg','./images/nzLamb/3.jpg'],
            paragraphs: ['Silver Fern Farms Lamb is grass-fed, free-range on lush, green pastures in New Zealand by passionate and skilled farmers, creating the subtle sweet flavour and tenderness it is renowned for. Our range of high-quality cuts deliver the taste so highly sought after by chefs and consumers around the world.'],
            icons: ['./images/icons/Icon_PastureRaised_GrassFed_black.svg','./images/icons/Icon-No Hormones_Black.svg','./images/icons/Icon-Halal_Logo_Black_2018.svg'],
            info: ['Grass-fed, pasture-raised | Sweet, subtle flavour','Succulently tender | Halal certified']
          }}/>
        </div>
        
        {this.navItems ? (
          <div className="meats-nav">
            {this.navItems.map((navItem, i) => {
              return <div className="meats-nav-item" style={{borderColor: navItem.color}} onClick={() => this.navClicked(i)}>{navItem.name}</div>
            })}
          </div>
        ) : (
          null
        )}
      </React.Fragment>
    )
  }
}


