import React, { Component } from 'react';

export default class Nav extends Component {
  lastClicked = () => document.querySelector(".nav-sffLogo").click();

  componentDidMount(){
    this.setRangeHeight();

    this.navItems = document.querySelectorAll(".nav-item");
    this.subItems = document.querySelectorAll(".nav-subList-item");
  }

  setRangeHeight(){
    this.subList = document.querySelector(".nav-subList");
    this.subList.style.height = this.subList.clientHeight + "px";
    this.subList.classList.add("-hidden");
  }

  toggleRange(e){
    if(e.target.classList[0].indexOf("subList") !== -1) return;

    document.querySelector(".nav-list").classList.toggle("-open");
    this.subList.classList.toggle("-hidden");
  }

  sublistClicked(i){
    this.navItems.forEach(item => {
      item.classList.remove("-active");
    })

    this.props.showMeats(i);

    this.lastClicked = () => this.sublistClicked(i);
  }

  mainSectionClicked(name, i){
    this.navItems.forEach(item => {
      item.classList.remove("-active");
    })

    this.subItems.forEach(item => {
      item.classList.remove("-active");
    })

    this.navItems[i].classList.add("-active");

    setTimeout(() => {
      document.querySelector(".nav").classList.remove("-active");
      document.querySelector("."+name).classList.add("-active");
      document.querySelector(".burger").classList.add("-active");
    }, 400)

    this.lastClicked = () => this.mainSectionClicked(name, i);
  }
    
  render(){
    return(
      <nav className="nav">
        <img src="./images/nav/SFF_Logo.svg" alt="" className="nav-sffLogo"/>
        <img src="./images/nav/close.svg" alt="" className="nav-close" onClick={() => this.lastClicked()}/>

        <ul className="nav-list">
          <li className="nav-item" onClick={() => this.mainSectionClicked("masterbrand", 0)}>Masterbrand</li>
          <li className="nav-item" onClick={() => this.mainSectionClicked("source", 1)}>Source</li>
          <li className="nav-item" onClick={() => this.mainSectionClicked("sustainability", 2)}>Sustainability</li>
          <li className="nav-item" onClick={e => this.toggleRange(e)}>
            Our Range
            <ul className="nav-subList">
              <li className="nav-subList-item" onClick={() => this.sublistClicked(0)}>Reserve</li>
              <li className="nav-subList-item" onClick={() => this.sublistClicked(1)}>Angus</li>
              <li className="nav-subList-item" onClick={() => this.sublistClicked(2)}>Cervena</li>
              <li className="nav-subList-item" onClick={() => this.sublistClicked(3)}>NZ Beef</li>
              <li className="nav-subList-item" onClick={() => this.sublistClicked(4)}>NZ Venison</li>
              <li className="nav-subList-item" onClick={() => this.sublistClicked(5)}>NZ Lamb</li>
            </ul>
          </li>
        </ul>
      </nav>
    )
  }
}


